@import url(https://fonts.googleapis.com/css?family=Roboto:400,100,900);
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body {
  height: 100vh;
  width: 100vw;
  background-image: linear-gradient(to right top, #a0b3d1, #87a3c9, #6b93c1, #4b84b9, #1875b1);
  background-repeat: no-repeat;
  overflow-x: hidden;
}
.navbar {
    display: flex;
    height: 80px;
    justify-content: flex-end;
    align-items: center;
}

.button {
    color: #fff;
    cursor: pointer;
    display: flex;
    justify-content: center;
    font-size:16px;
    font-weight: 400;
    line-height: 45px;
    max-width: 160px; 
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    width: 100%; 
    margin-right: 18px;
    border-radius: 3px;
}

.button-5 {
    border: 0 solid;
    box-shadow: inset 0 0 20px rgba(255, 255, 255, 0);
    outline: 1px solid;
    outline-color: rgba(255, 255, 255, .5);
    outline-offset: 0px;
    text-shadow: none;
    transition: all 1250ms cubic-bezier(0.19, 1, 0.22, 1);
} 
  
.button-5:hover {
    border: 1px solid;
    box-shadow: inset 0 0 20px rgba(255, 255, 255, .5), 0 0 20px rgba(255, 255, 255, .2);
    outline-color: rgba(255, 255, 255, 0);
    outline-offset: 15px;
    text-shadow: 1px 1px 2px #427388; 
}

.button:first-child:hover, :not(.button-check)+.button:hover {
    color: lightskyblue
}
 
@media (max-width: 992px) { 
  .navbar {
    height: 52px;
  }
  .button {
    line-height: 32px;
    max-width: 100px;
  }
}
.parallax-effect-glare-scale {
    margin-left: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 150px;
    background-image: linear-gradient(to right top, #a0b3d1, #87a3c9, #6b93c1, #4b84b9, #1875b1);
    color: white;
    border-radius: 3px;
}

#logo {
    width: 100px;
}

@media (max-width: 992px) { 
    .parallax-effect-glare-scale {
        width: 100px;
        height: 100px;
    }
    #logo {
        width: 65px;
    }
}
.conteiner {
    margin-top: 2rem;
}
.form {
    margin: auto;
    display: flex;
    justify-content: center;
    width: 35%;
    height: 14vh;
    align-items: center;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    background-color: #F8BA43;
    opacity: 0.8;
    background: linear-gradient(135deg, #ab6a1f55 25%, transparent 25%) -10px 0/ 20px 20px, linear-gradient(225deg, #ab6a1f 25%, transparent 25%) -10px 0/ 20px 20px, linear-gradient(315deg, #ab6a1f55 25%, transparent 25%) 0px 0/ 20px 20px, linear-gradient(45deg, #ab6a1f 25%, #e98100 25%) 0px 0/ 20px 20px;
}

.form > input {
    width: 70%;
    height: 40px;
    outline: none;
    border-radius: 5px;
    border: none;
    margin-right: 5px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 16px;
}

.form > button {
    width: 20%;
    height: 40px;
    border-radius: 5px;
    border: 1px solid blueviolet;
    background-color:blueviolet;
    transition: all 0.2s;
    cursor: pointer;
    color: white;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 16px;
    margin-left: 5px;
}

.form > button:hover {
    background-color: darkblue;
    border: 1px solid blueviolet;
}

#textIntro {
    text-align: center;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 22px;
    color: black;
}

@media (max-width: 992px) { 
    .form {
        margin: auto;
        display: flex;
        justify-content: center;
        width: 90%;
        height: 14vh;
        align-items: center;
        border-radius: 5px;
        box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
        background-color: #F8BA43;
        opacity: 0.8;
        background: linear-gradient(135deg, #ab6a1f55 25%, transparent 25%) -10px 0/ 20px 20px, linear-gradient(225deg, #ab6a1f 25%, transparent 25%) -10px 0/ 20px 20px, linear-gradient(315deg, #ab6a1f55 25%, transparent 25%) 0px 0/ 20px 20px, linear-gradient(45deg, #ab6a1f 25%, #e98100 25%) 0px 0/ 20px 20px;
    }
    #textIntro {
        text-align: center;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
